<script>
import Header from "../../components/global/header/HeaderArea.vue"
import Footer from "../../components/global/footer/FooterArea.vue"
import { ref, onBeforeMount } from 'vue'
import { useRouter } from "vue-router"
import { Toast, ToastConfirm } from '@/components/global/swal'
import { apiGetCareRecipientInfo, apiDeleteCareRecipientInfo } from "@/api/api"
import { getUrlId } from "@/utils/handle"


export default {
  setup() {
    const router = useRouter()
    const data = ref([])
    const urlId = getUrlId().urlId

    // let eatTypeInfo = [];
    let abilityToActInfo = [];
    let servicePlaceInfo = ref("");

    //路由
    const goto = (val) => {
      router.push({ path: val })
    }

    const deleteData = async () => {
      const result = await ToastConfirm("確定刪除此筆資料")
      if (result == true) {
        let response = await apiDeleteCareRecipientInfo({ id: urlId })
        if(response.status){
          Toast('資料已刪除')
          goto('/careRecipient/list')
        }else{
          const errorMessage = response.message || "錯誤"
          Toast(errorMessage)
        }

      }
    }

    onBeforeMount(async () => {
      //getData
      try {
        data.value = await apiGetCareRecipientInfo({ id: urlId })
        data.value = data.value.data
        // console.log(data.value)
        // data.value.eatType.forEach(function (item, index) {
        //   eatTypeInfo.push(item.name);
        // });

        data.value.abilityToAct.forEach(function (item, index) {
          abilityToActInfo.push(item.name);
        });
        if (data.value.servicePlace.placeType.id == 1) {
          servicePlaceInfo.value = data.value.servicePlace.city.name + data.value.servicePlace.area.name;
        } else {
          servicePlaceInfo.value = data.value.servicePlace.city.name + data.value.servicePlace.area.name + data.value.servicePlace.place.address;
        }
      } catch (error) {
        console.log(error)
      }


    })

    return { data, abilityToActInfo, servicePlaceInfo, goto, urlId, deleteData };
  },
  components: {
    Header, Footer
  },
};
</script>

<template>
  <div class="careRecipientInfo">
    <Header title="資料內容" backToPath="/careRecipient/list" />
    <div class="main">
      <div class="title">{{ data.name }}</div>
      <div class="main1">
        <div class="title1">性別</div>
        <div class="content1">{{ data.gender == "Female" ? "女性" : "男性" }}</div>
      </div>
      <div class="main1">
        <div class="title1">年齡</div>
        <div class="content1">{{ data.age }}歲</div>
      </div>
      <div class="main1">
        <div class="title1">身高</div>
        <div class="content1">{{ data.height }}公分</div>
      </div>
      <div class="main1">
        <div class="title1">體重</div>
        <div class="content1">{{ data.weight }}公斤</div>
      </div>
      <div class="main1">
        <div class="title1">關係</div>
        <div class="content1">{{ data.relationship }}</div>
      </div>
      <!-- <div class="main2">
        <div class="title2">是否有傳染力</div>
        <div class="content2">{{ data.isContagious ? "是" : "否" }}</div>
      </div>
      <div class="main2">
        <div class="title2">飲食類型</div>
        <div class="content2">{{ eatTypeInfo.join('、') }}</div>
      </div> -->
      <div class="main2">
        <div class="title2">行動能力</div>
        <div class="content2">{{ abilityToActInfo.join('、') }}</div>
      </div>
      <div class="main2">
        <div class="title2">照顧地點</div>
        <div class="content2">{{ servicePlaceInfo }}</div>
      </div>
    </div>
    <hr class="divider">
    <div class="main">
      <div class="title">病症</div>
      <div v-if="data?.disease?.length > 0">
        <div class="main2" v-for="item in data.disease" :key="item.id">
          <div class="title2">{{ item.diseaseName }}</div>
          <div class="content2">{{ item.symptom.join('、') }}</div>
        </div>
      </div>
      <div v-if="data?.disease?.length == 0">
        <div class="main2">
          <div class="title2">無病症資料</div>
        </div>
      </div>
    </div>
    <hr class="divider">
    <div class="btnArea">
      <button type="button" class="btn btn-outline-primary" @click="goto(`/careRecipient/edit/${urlId}`)">修改資料</button>
      <button type="button" class="btn btn-outline-primary" @click="deleteData">刪除資料</button>
    </div>
    <Footer />
  </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.careRecipientInfo {
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
  min-height: calc(100vh - $footer-height - 15px - $header-height);

  .main {
    padding-top: 22px;
    padding-left: 22px;
    padding-right: 22px;

    .title {
      font-size: 1.125rem;
      font-weight: 700;
      color: #000000;
      margin-bottom: 16px;
    }

    .main1 {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      margin-top: 5px;
      margin-bottom: 5px;

      .title1 {
        font-size: 14px;
        font-weight: 400;
        color: #909090;
        margin-right: 14px;

      }

      .content1 {
        font-size: 16px;
        font-weight: 400;
        color: #333333;
      }
    }

    .main2 {
      margin-top: 10px;
      margin-bottom: 10px;

      .title2 {
        font-size: 14px;
        font-weight: 400;
        color: nth($secondary-color, 1);
      }

      .content2 {
        font-size: 16px;
        font-weight: 400;
        color: #333333;
      }

    }
  }

  .divider {
    margin: 0 22px;
  }

  .btnArea {
    margin: 15px 0px;
    display: flex;
    justify-content: space-evenly;
  }

}
</style>